<template>
  <div>

    <!--  -->
    <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        border
        stripe
    >
      <el-table-column
          prop="filename"
          label="">
      </el-table-column>
      <el-table-column
          prop="icon"
          width="260px"
          label="">

        <template slot-scope="scope">
          <!--        <el-button  type="button" @click="download(scope.row.filename)">  </el-button>-->
          <a :href="'http://localhost:8081/teacher/file/download?fileName='+scope.row.filename"></a>
          <el-divider direction="vertical"></el-divider>

        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "StuCourseDetail",
  data () {
    return {
      uploadUrl: 'http://localhost:8081/teacher/file/upload',
      fileList: [],
      downloadlist:[],
      tableData: [],
    }
  },
  created() {
    this.getFileList()
  },
  methods: {

    handleExceed(files, fileList) {
      this.$message.warning(` 5  ${files.length}  ${files.length + fileList.length} `);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${ file.name }`);
    },
    handleUploadError(error, file) {
      console.log(""+error)
      // this.$notify.error({
      //          title: 'error',
      //          message: ':' +  error,
      //          type: 'error',
      //          position: 'bottom-right'
      //        })
    },
    //(web)
    handleBeforeUpload(file){
      this.uploadUrl = 'http://localhost:8081/teacher/file/upload'
      console.log(""+file)
      let formData = new FormData();
      formData.append("multipartFiles", file);

      this.$axios.post('/teacher/file/upload',formData,{
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        params:{
          'cno':this.$route.params,
        }
      }).then((res) => {
        console.log(""+res)
        this.$router.go(0);
      }).catch(error => {
        console.log(":"+error)
      })
    },
    //
    getFileList(){
      this.$axios.get('/teacher/file/getFileList',{
        params:{
          'cno':this.$route.params,
        }
      }).then((res) => {
        console.log(res.data.data.records)
        this.tableData = res.data.data.records
      })
    },
    //
    download(fileName){
      this.$axios.get('/teacher/file/download?fileName='+fileName,{timeout: 1000})
    }
  }
}
</script>

